<template>
  <div class="group_info">
    <head-top title="小组详情">
      <router-link v-if="$store.getters.groupID" class="head-right" to="/teamRank">
        <i class="iconfont icon-chengyuan"></i>
      </router-link>
    </head-top>
    <div class="cell">基本信息</div>

    <div class="info">
      <div class="info_head">
        <span>{{groupName}}</span>
        <div v-if="!$store.getters.groupID">
          <van-button type="primary" size="mini" @click="joinGroup()">加入</van-button>
        </div>
        <div v-if="$store.getters.studentID === dataInfo.studentID && dataList.length > 0 " class="info_oprate">
          <van-button type="primary" size="mini" @click="openUpdateGroupName">修改组名</van-button>
        </div>
      </div>
      <div class="info_content">
        <span>小组人数：{{dataInfo.groupMax}}人</span>
        <span>已加入人数：{{dataList.length}}人</span>
        <div></div>
      </div>
      <div class="info_foot">
        <span class="msg">成员:</span>
        <span>{{memberNames}}</span>
      </div>
    </div>

    <div class="cell">小组成员</div>
    <div class="list" v-if="dataList.length > 0">
      <div class="list_item" v-for="(item, index) in dataList" :key="index">
        <img :src="defaultHead" v-real-img="[ item['UserID'], item['StudentID'] ]" alt/>
        <span
          :class=" dataInfo.studentID === item['StudentID'] ?'member leader':'member normal'">{{ dataInfo.studentID === item['StudentID'] ?'组长':'成员' }}</span>
        <div class="list_info">{{item['StudentName']}}</div>
      </div>
    </div>
    <div class="list_item" v-else style="text-align:center;">暂无成员~</div>

    <div class="footer">
      <van-button type="primary" size="small" v-if="$store.getters.groupType===2 && $store.getters.groupID"
                  @click="quitGroup()">退出小组
      </van-button>
    </div>

    <!-- 小组名称修改 -->
    <van-popup v-model="showEdit" class="pop_dialog" @close="closeUpdateGroupName">
      <div class="pop_head">修改小组名称</div>
      <van-field type="text" placeholder="请输入小组名称" v-model="groupName" @keyup.enter="updateGroupName"
                 :style="{padding:'0 20px'}"/>
      <van-button type="primary" @click="updateGroupName">提交</van-button>
    </van-popup>
  </div>
</template>

<script>
import { Dialog } from "vant";

export default {
  name: "",
  components: { [Dialog.Component.name]: Dialog.Component },
  props: {},
  beforeRouteLeave (to, from, next) {
    if (this.$store.getters.groupID && to.path === "/joinGroup") {
      next(false);
      this.$router.push("/course");
    } else {
      next();
    }
  },
  data () {
    return {
      defaultHead: require("./../../../assets/default_head.png"),
      dataList: [],
      groupList: [],
      showEdit: false,
      groupName: "暂无组名",
      oldGroupName: "",
      dataInfo: {}
    };
  },
  created () {
    this.getDataInfo();
    this.getGroupDataInfo();
  },
  methods: {
    getDataInfo () {
      this.dataList = [];
      this.$mainHttp.group.getRankList({
        groupid: this.$store.getters.groupID || this.$route.params.groupId
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          this.dataList = [];
          res.Datas.forEach(item => {
            if (this.dataList.indexOf(item) === -1) this.dataList.push(item);
          });
        }
      }).catch(() => {
      });
    },
    getGroupDataInfo () {
      this.$store.dispatch("GetGroupByUser").then(res => {
        if (!res.Result && !res.IsGrouping) {
          this.$toast("当前尚无分组");
        } else if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            this.groupList = [];
            res.Datas.forEach(item => {
              let groupItem = {
                gatewayID: item.Key["GatewayID"],
                groupID: item.Key["GroupID"],
                groupName: item.Key["GroupName"],
                groupMax: item.Key["GroupNumber"],
                groupingID: item.Key["GroupingID"],
                studentID: item.Key["StudentID"],
                currentNum: item.Total,
                gatewayIP: item["GatewayIP"]
              };
              this.groupList.push(groupItem);
            });

            this.dataInfo = this.groupList.find(
              r => r.groupID === this.$route.params.groupId
            );
            this.groupName = this.dataInfo.groupName || "暂无组名";
          } else if (res.Data) {
            this.dataInfo = {
              gatewayID: res.Data["GatewayID"],
              groupID: res.Data["GroupID"],
              groupName: res.Data["GroupName"],
              groupMax: res.Data["GroupNumber"],
              groupingID: res.Data["GroupingID"],
              studentID: res.Data["StudentID"],
              currentNum: 0,
              gatewayIP: res["GatewayIP"] || ""
            };
            this.groupName = this.dataInfo.groupName || "暂无组名";
          }
        }
      }).catch(() => {
        this.groupList = [];
      });
    },
    quitGroup () {
      Dialog.confirm({
        title: "退出小组",
        message: "是否退出当前小组？"
      }).then(() => {
        this.$store.dispatch("QuitGroup").then(res => {
          if (res.Result) {
            this.$toast("退出小组成功");
            this.$router.push("/joinGroup");
          } else {
            this.$toast(res.Info || "退出小组失败");
          }
        }).catch(() => {
          this.$toast("退出小组失败");
        });
      }).catch(() => {
        // on cancel
      });
    },
    joinGroup () {
      let groupInfo = this.dataInfo;
      Dialog.confirm({
        title: "加入小组",
        message: "是否加入组：" + groupInfo.groupName
      }).then(() => {
        this.$store.dispatch("JoinGroup", {
          groupID: groupInfo.groupID,
          group: groupInfo
        }).then(res => {
          if (res.Result) {
            this.$toast("加入成功");
            this.getGroupDataInfo();
            this.getDataInfo();
            // this.getDataInfo();
          } else if (res.Info) {
            this.$toast(res.Info);
          } else {
            this.$toast("加入失败");
          }
        }).catch(() => {
          this.$toast("操作失败");
        });
      }).catch(() => {
        // on cancel
      });
    },
    openUpdateGroupName () {
      this.oldGroupName = this.groupName;
      this.showEdit = true;
    },
    updateGroupName () {
      if (this.groupName.length < 1 || this.groupName === "暂无组名") {
        this.$toast("请输入小组名称");
      } else {
        this.$mainHttp.group.postDataSave({
          ciID: this.$store.getters.courseID,
          groupID: this.$store.getters.groupID,
          groupName: this.groupName,
          studentID: this.$store.getters.studentID
        }).then(res => {
          if (res.Result) {
            this.$toast("组名修改成功");
            this.oldGroupName = this.groupName;
            this.showEdit = false;
          } else {
            this.$toast(res.Info || "组名修改失败");
            this.groupName = this.dataInfo.groupName || "暂无组名";
          }
        }).catch(() => {
          this.$toast("组名修改失败");
          this.groupName = this.dataInfo.groupName || "暂无组名";
        });
      }
    },
    closeUpdateGroupName () {
      if (this.oldGroupName !== this.groupName) {
        this.groupName = this.oldGroupName;
      }
    }
  },
  computed: {
    memberNames () {
      return this.dataList
        .map(r => {
          return r["StudentName"];
        })
        .join("、");
    }
  },
  watch: {
    "$store.getters.group" (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.getDataInfo();
        this.dataInfo = newVal;
        this.groupName = this.dataInfo.groupName || "暂无组名";
        // this.getGroupDataInfo();
      }
    }
  }
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");

.group_info {
  width: 100%;
  height: 100%;
  background: #ebf0ef;

  .cell {
    font-size: @font-info;
    line-height: 2.5;
    text-align: left;
    margin: 0 3vw;
  }

  .info {
    width: 94vw;
    padding: 1vh 3vw;
    background-color: #fff;
    font-size: @font-info;
    line-height: 2;
    color: #666;

    .info_head {
      display: flex;
      justify-content: space-between;
      align-items: baseline;
      font-size: @font-sub-title;
      font-weight: bolder;
      line-height: 2;
      color: #333;

      .info_oprate {
        line-height: unset;
      }
    }

    .info_content {
      display: flex;
      justify-content: space-between;
    }

    .info_foot {
      display: flex;
      justify-content: left;
      text-align: left;

      .msg {
        min-width: 40px;
      }
    }
  }

  .list {
    width: 96vw;
    padding: 2vh 2vw 0 2vw;
    background-color: #fff;
    display: flex;
    justify-content: start;
    flex-wrap: wrap;

    font-size: @font-info;
    line-height: 2;
    color: #666;

    .list_item {
      width: 20vw;
      margin: 0 2vw;
      padding-bottom: 3vh;
      position: relative;

      img {
        margin: 0 auto;
        max-width: 20vw;
        max-height: 10vh;
        border-radius: 50%;
        border: 1px solid #00cbab;
      }

      .member {
        position: absolute;
        bottom: calc(3.2vh + 25px);
        left: 50%;
        transform: (translateX(-50%));
        // width: 9vw;
        padding: 0 2vw;
        width: 25px;
        height: 20px;
        line-height: 20px;
        border-radius: 3px;
        font-size: @font-info;
        color: #fff;
        text-align: center;
      }

      .leader {
        background: rgba(247, 163, 65, 1);
      }

      .normal {
        background: #00cbab;
      }

      .list_info {
        padding-top: 1vh;
      }
    }
  }

  .footer {
    margin: 2vh 0;

    .van-button {
      width: 60vw;
      border-radius: 5px;
    }
  }

  .pop_dialog {
    width: 70vw;
    // height: 20vh;
    font-size: @font-sub-title;
    line-height: 2;

    .pop_head {
      margin: 1.5vh 0;
    }

    input {
      background-color: #f8f8f8;
      border: 1px solid #f2f2f2;
      color: #323233;
      // padding: 0 1vw;
      width: 90%;
    }

    button {
      margin: 1.7vh 0;
      width: 90%;
      // color: #fff;
      // background-color: #00cbab;
    }
  }
}
</style>



